@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
* {
  font-family: 'Dancing Script', cursive;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  height: 100vh;
  background: #C6DEF1;
  background: linear-gradient(to bottom right, #C6DEF1, #DBCDF0);
  margin: 0;
  padding: 10px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 500px) {
  body {
    font-size: 14px;
  }
}
.App{margin:100px auto 0 auto;padding:20px;max-width:700px;background:coral;color:#000;border-radius:10px;box-shadow:-7px 7px 5px 0px rgba(50,50,50,.75)}@media(max-width: 900px){.App{margin:0 auto}}
.Header{display:flex;justify-content:space-between;align-items:center}.Header__title{font-family:"Dancing Script",cursive;font-size:20px}
.Search{margin-top:10px;display:flex;justify-content:flex-start;grid-gap:10px;gap:10px}.Search__input{width:500px;max-width:500px;padding:5px}.Search__button{padding:5px;cursor:pointer}.Search__button--active{background-color:gold;color:#000}
.PostsList{margin-top:20px;list-style:none;display:flex;flex-flow:column;grid-gap:20px;gap:20px}
.PostListItem{border:1px solid gold;border-radius:5px;padding:5px;display:flex;justify-content:space-between;align-items:center}.PostListItem__text{cursor:pointer}.PostListItem__icons{display:flex;justify-content:space-between;align-items:center;grid-gap:5px;gap:5px;cursor:pointer}.important{color:#8a2be2}.liked{color:red}
.AddPost{margin-top:20px;display:flex;justify-content:flex-start;grid-gap:10px;gap:10px}.AddPost__input{width:500px;max-width:500px;padding:5px}.AddPost__button{padding:5px;cursor:pointer}
